
/************************************* HEADER ***********************************************/


  .header {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "logo other other";
    width: 90%;
    margin: 0 auto;
    padding-top: 2em;
    padding-bottom: 0;
    align-items: center;
    position: relative;
  }

  .header__logo {
    font-family: var(--font-logo);
    font-weight: 400;
    color: var(--color_accent_two);
    font-size: calc(var(--fontXXL) * 1.3);
    margin: 0;
    width: 33%;
    grid-area: logo;    
  }

  .header__nav {
    margin-left: 4.5em;
  }

  .header__mobile {
    grid-area: other;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid yellow; */
  }
  
  .header__nav__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    position: relative;
  }
  
  .header__signing {
    width: 35%;
   /*  border: 1px solid green; */
  }
  
  .header__nav__item,
  .header__signing__item {
    font-size: calc(var(--fontXS)*1.2);
    padding: .3em .5em;
    border-radius: .75em;
    text-align: center;
  }
  
  .header__nav__item {
    position: relative;
  }
  
  .header__signing__item {
    font-size: var(--fontXS);
    font-weight: 700;
    padding: .5em 1em;
  }
  
  .header__signing__items {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    /* border: 2px solid red; */
  }
  
  .in {
    color: var(--color_accent_two);
    border: solid 1px var(--color_accent_two); 
    margin-right: 1em;
  }
  
  .up {
    background-color: var(--color_accent_one);
    color: var(--color_primary);
    border: solid 1px var(--color_accent_one);
  }

/************************************* MEDIA ***********************************************/


@media (max-width: 1200px) {
	
}

@media (max-width: 992px) {	
	
}

@media (max-width: 768px) {
  .header__signing {
    width: 45%;
  }
}

@media (max-width: 580px) {
  .mobile-bg {
    background-color: var(--color_primary);
    position: absolute;
    width: 100vw;
    height: 100vh;
    right: 0;
    z-index: 50;
  }

  .hamburger__button {
    display: flex;
    position: fixed;
    right: 2em;
    top: 4em;
    z-index: 100;
  }

  .header__mobile {
    position: fixed;
    top: 20%;
    right: 10%;
    z-index: 90;
    display: flex;
    flex-direction: column;
    border: 1px solid red;
  }

  .header__signing__items,
  .header__nav__items {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 3em;
    border: 1px solid green;
    margin: 0;
    padding: 0;
  }

  .header__signing__items {
    margin-top: 3em;
    text-align: end;
  }

  .header__signing__item,
  .header__nav__item {
    font-size: 3rem;
    margin: 0;
    border: none;
  }

  .up, .in {
    background-color: var(--color_primary);
    color: var(--color_accent_one);
    margin: 0;
    font-weight: 400;
  }

}

